<template>
  <!--  -->
  <div class="collection">
    <div class="bg">
      <img src="~@/assets/images/collection/bg@2x.png" alt="" />
    </div>
    <div class="cont">
      <div class="info">
        <div class="info-left">
          <user-info></user-info>
        </div>
        <div class="info-right">
          <div class="rbox" @click="myCollect">
            
            <img
              class="btn"
              src="~@/assets/images/collection/right@2x.png"
              alt=""
            />
          </div>
          <div class="rbox" @click="showSort">
            <img
              class="btn"
              src="~@/assets/images/collection/right@2x.png"
              alt=""
            />
          </div>
          <div class="rbox" @click="viewNotice">
            <img
              class="btn"
              src="~@/assets/images/collection/right@2x.png"
              alt=""
            />
          </div>
        </div>
      </div>
      <div class="swiper-container">
        <div class="swiper-wrapper">
          <div class="swiper-slide pr">
            <div class="title"></div>
            <img class="gu" src="../assets/images/collection/digital1@2x.png" />
            <img
              src="../assets/images/collection/btn_go@2x.png"
              class="btn"
              @click="goDetail(1)"
            />
          </div>
          <div class="swiper-slide">
            <div class="title"></div>
            <img class="gu" src="../assets/images/collection/digital2@2x.png" />
            <img
              src="../assets/images/collection/btn_go@2x.png"
              class="btn"
              @click="goDetail(2)"
            />
          </div>
          <div class="swiper-slide">
            <div class="title"></div>
            <img
              class="gu"
              src="../assets/images/collection/digital_wait@2x.png"
            />
            <!-- <img
              src="../assets/images/collection/btn_go@2x.png"
              class="btn"
              @click="goDetail(1)"
            /> -->
          </div>
        </div>
      </div>
    </div>

    <Modal class="modal" :visible.sync="isNoticeShow" title="">
      <div class="modal-content" slot="content">
        <div class="text mb-24"></div>
        <div class="sub-text mb-32">
          
        </div>
      </div>
    </Modal>
    <Modal class="modal" :visible.sync="isModalShow6" title="">
      <div class="modal-content" slot="content">
        <div class="text mb-24"></div>
        <div class="sub-text mb-32"></div>
      </div>
      <div class="modal-footer" slot="footer">
        <img src="@/assets/images/common/modal_btn_ljqw.png" alt="" />
      </div>
    </Modal>

    <div v-show="isShowSort" class="sort-modal">
      <div class="mask"></div>
      <div class="box">
        <div class="box-bg-top"></div>
        <div class="box-bg-bottom">
          <span @click="hideModal" class="close"></span>
        </div>
        <div class="main">
          <div class="main-content">
            <div class="sort-wrap">
              <div class="sort-title">
                <div class="sort-num justify-center">
                  <div class=""></div>
                </div>
                <div class="sort-name sort-text align-center"></div>
                <div class="invite-count sort-text align-center"></div>
              </div>
              <div class="my-sort">
                <div class="sort-num justify-center">
                  <div v-if="mySort.sort > 9999" class="my-sort-digital">
                    --
                  </div>
                  <div v-else class="sort-digital">
                    {{ mySort.sort }}
                  </div>
                </div>
                <div class="sort-name sort-text align-center"></div>
                <div class="invite-count sort-text align-center">
                  {{ mySort.inviteNum }}
                </div>
              </div>
              <div class="sort-box">
                <div
                  class="sorti"
                  v-for="(sort, index) in sortList"
                  :key="index"
                >
                  <div class="sort-num justify-center">
                    <div v-if="index === 0" class="sort-top">
                      <img
                        class="sortimg"
                        src="~@/assets/invite/sort1@2x.png"
                        alt=""
                      />
                    </div>
                    <div v-else-if="index === 1" class="sort-top">
                      <img
                        class="sortimg"
                        src="~@/assets/invite/sort2@2x.png"
                        alt=""
                      />
                    </div>
                    <div v-else-if="index === 2" class="sort-top">
                      <img
                        class="sortimg"
                        src="~@/assets/invite/sort3@2x.png"
                        alt=""
                      />
                    </div>
                    <div v-else class="sort-digital">{{ sort.sort }}</div>
                  </div>
                  <div class="sort-name sort-text align-center">
                    {{ sort.name }}
                  </div>
                  <div class="invite-count sort-text align-center">
                    {{ sort.inviteNum }}
                  </div>
                </div>
              </div>
            </div>
            <!-- <slot name="footer"></slot> -->
          </div>
          <div class="tips"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import { utils } from '@/utils/utils'

import UserInfo from '@/components/userInfo'
import Modal from '@/components/commonModal'

import { Swiper } from 'swiper'

// Import Swiper styles
// import 'swiper/css'
import 'swiper/swiper.less'

export default {
  data () {
    return {
      isVcodeShow: false,
      phone: '',
      code: '',
      isLoading: false,
      countdown: 0,
      isNoticeShow: false, // 
      isModalShow5: false, // 
      isModalShow6: false, // 
      isShowSort: false,
      sortList: [
        {
          sort: 1,
          name: '',
          inviteNum: 2222304
        },
        {
          sort: 2,
          name: '',
          inviteNum: 230333
        },
        {
          sort: 3,
          name: '12345678912345678',
          inviteNum: 23304
        },
        {
          sort: 4,
          name: '',
          inviteNum: 2304
        },
        {
          sort: 5,
          name: '',
          inviteNum: 2304
        },
        {
          sort: 6,
          name: '',
          inviteNum: 23
        },
        {
          sort: 7,
          name: '',
          inviteNum: 23
        },
        {
          sort: 8,
          name: '',
          inviteNum: 23
        },
        {
          sort: 9,
          name: '',
          inviteNum: 23
        },
        {
          sort: 10,
          name: '',
          inviteNum: 21
        }
      ],
      mySort: {
        sort: 12000,
        inviteNum: 2
      }
    }
  },
  components: {
    Modal,
    UserInfo,
    Swiper
    // SwiperSlide
  },
  mounted () {
    let mySwiper = new Swiper('.swiper-container', {
      loop: true,
      slidesPerView: 2,
      spaceBetween: 30,
      centeredSlides: true,
      watchSlidesProgress: true,
      on: {
        setTranslate: function () {
          let slides = this.slides
          for (let i = 0; i < slides.length; i++) {
            let slide = slides.eq(i)
            let progress = slides[i].progress
            // console.log(progress)
            slide.css({ 'opacity': '', 'background': '' })
            // slide.transform('')// 
            slide.css('opacity', (1 - Math.abs(progress) / 2))
            slide.transform('scale(' + (1 - Math.abs(progress) / 6) + ')')
          }
        },
        setTransition: function (transition) {
          for (var i = 0; i < this.slides.length; i++) {
            var slide = this.slides.eq(i)
            slide.transition(transition)
          }
        }
      }
    })
    console.log(mySwiper)
  },
  methods: {
    goDetail (index) {
      this.$router.push({ name: 'digitalShop', params: { type: index } })
    },
    viewNotice () {
      this.isNoticeShow = true
    },
    myCollect () {
      this.$router.push('digitalMyCollection')
    },
    showSort () {
      this.isShowSort = true
    },
    hideModal () {
      // this.$emit('update:visible', false)
      this.isShowSort = false
    }
  }
}
</script>

<style lang="less" scoped>
@import url("../assets/css/mixin.less");
.collection {
  height: 100%;
  width: 100%;
  .bg {
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .cont {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    bottom: 0;
    .flex_func(column, flex-start, center, nowrap);
    .info {
      width: 100%;
      height: 240px;
      // position: absolute;
      margin-top: 150px;
      z-index: 4;
      .flex_func(row, space-between, flex-end, nowrap);
      .info-left {
        width: 400px;
        height: 100%;
        padding-top: 40px;
        .flex_func(column, flex-start, flex-start, nowrap);
      }
      .info-right {
        width: 190px;
        height: 100%;

        // padding-right: 20px;
        .flex_func(column, center, flex-end, nowrap);
        .rbox {
          width: 190px;
          height: 72px;
          margin-bottom: 5px;
          .bg-image("~@/assets/images/collection/btn_bg","png");
          line-height: 72px;
          text-align: right;
          font-size: 28px;
          color: #c75935;
          .btn {
            width: 20px;
            height: 20px;
            margin: 0 8px 0 5px;
          }
        }
      }
    }
    .swiper-container {
      width: 100%;
      height: calc(100% - 500px);
      // background: rgba(0, 0, 0, 0.5);
      padding: 140px 0;
      margin-left: auto;
      margin-right: auto;
    }
  }
}
.swiper-slide {
  text-align: center;
  font-size: 18px;
  height: 100%;
  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
  transition-property: all;
  // background: linear-gradient(180deg, #ffe299 0%, #da0000 100%);
  // opacity: 0.3;
  // border: 2px solid #ffe299;
  // border-radius: 30px;
  .title {
    position: absolute;
    top: 20px;
    left: 10px;
    min-width: 120px;
    margin-left: 20px;
    padding: 0 15px;
    height: 50px;
    background: #000000;
    border-radius: 25px;
    opacity: 0.4;
    font-size: 28px;
    font-family: PingFangSC-Semibold, PingFang SC;
    font-weight: 600;
    color: #ffffff;
    line-height: 50px;
    &::before {
      content: "";
      width: 10px;
      height: 10px;
      background: #000000;
      opacity: 0.8;
      position: absolute;
      left: -15px;
      top: 20px;
      border-radius: 10px;
    }
  }
  .gu {
    width: 120%;
    height: 100%;
  }
  .btn {
    width: 296px;
    height: 112px;
    position: absolute;
    bottom: 20px;
  }
}
.swiper-slide-active {
  // .bg-image("~@/assets/images/collection/bg3","png");
  opacity: 1;
}

.sort-modal {
  z-index: 4;
  .mask {
    position: fixed;
    z-index: 5;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.65);
  }
  .box {
    box-sizing: border-box;
    position: fixed;
    z-index: 6;
    width: 610px;
    min-height: 400px;
    padding: 150px 0 40px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-image: url(~@/assets/images/collection/light@2x.png);
    background-position: 0 100%;
    background-repeat: no-repeat;
    background-size: 100% 112%;
    .box-bg-top {
      position: absolute;
      top: 0;
      background-image: url(~@/assets/images/collection/sort_top@2x.png);
      background-repeat: no-repeat;
      background-size: 100%;
      width: 100%;
      height: 150px;
    }
    .main {
      background: linear-gradient(130deg, #fff1d5 0%, #db8d1e 100%);
      border-radius: 52px;
      border-left: 4px solid #feeebc;
      border-right: 4px solid #feeebc;
      padding: 12px;
      .main-content {
        padding: 20px 30px;
        text-align: center;
        max-height: 900px;
        background: rgba(255, 255, 255, 0.58);
        border-radius: 42px;
        overflow-y: auto;
        .sort-wrap {
          width: 100%;
          border-radius: 16px;
          .sort-title {
            width: 100%;
            height: 88px;
            line-height: 88px;
            font-size: 28px;
            font-family: PingFangSC-Medium, PingFang SC;
            font-weight: 500;
            color: #9d2e10;
            .flex_func(row,space-between,center,nowrap);
            .sort-text {
              color: #9d2e10;
            }
          }
          .my-sort {
            height: 96px;
            width: 100%;
            // background: linear-gradient(180deg, #f5deb1 0%, #ffd08d70 100%);
            .bg-image("~@/assets/images/collection/my_sort","png");
            .flex_func(row,space-between,center,nowrap);
            .my-sort-digital {
              border-radius: 20px;
              font-size: 24px;
              text-align: center;
              line-height: 40px;
            }
          }
          .sort-box {
            // padding: 0 56px;
            // background: #fef0f2;
            border-bottom-left-radius: 16px;
            border-bottom-right-radius: 16px;
            .sorti {
              width: 100%;
              height: 96px;
              // border-top: 2px solid #efefef;
              .flex_func(row,space-between,center,nowrap);
            }
          }
          .sort-num {
            width: 100px;
            .sort-top {
              .sortimg {
                width: 60px;
                height: 76px;
              }
            }
            .sort-digital {
              width: 40px;
              height: 40px;
              border-radius: 20px;
              // border: 1px solid #ead5d5;
              font-size: 24px;
              font-family: PingFangSC-Medium, PingFang SC;
              font-weight: 500;
              color: #cc6d5b;
              text-align: center;
              line-height: 40px;
            }
          }
          .sort-name {
            width: calc(100% - 210px);
            height: 100%;
            padding-left: 15px;
          }
          .invite-count {
            width: 110px;
            height: 100%;
          }
          .sort-text {
            font-size: 28px;
            color: #9d2e10;
            text-align: left;
          }
        }
        .text {
          font-size: 32px;
          color: #333;
          line-height: 1.5;
        }
        .sub-text {
          font-size: 24px;
          color: #666;
        }
        .modal-footer {
          img {
            width: 348px;
            height: 128px;
          }
        }
        .modal-footer-row-two {
          display: flex;
          img {
            width: 288px;
            height: 128px;
          }
        }
      }
      .tips {
        width: 100%;
        height: 60px;
        font-size: 24px;
        color: #ffffff;
        line-height: 60px;
        text-align: center;
      }
    }
    .box-bg-bottom {
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 45px;
      text-align: center;
      .close {
        position: absolute;
        margin-left: -44px;
        background-image: url(~@/assets/images/common/modal_close.png);
        background-repeat: no-repeat;
        background-position: center;
        background-size: 28px 28px;
        width: 88px;
        height: 88px;
      }
    }
  }
}
</style>



